@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;300;400&family=Poppins:wght@200;300;400&display=swap');

body,html{
    font-family: 'Poppins';
    background: #141414;
}
.main-container{
    padding: 2rem;
    margin-top: 3rem;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.card{
    margin: 1rem;
    position: relative;
    background-color:transparent !important;
    transition: .2s ease;
    border-radius: 6px;
    min-width: 200px;
}
.navbar{
    background: rgb(15,15,15,0.8) !important;
    position: fixed;
    top: 0;
    z-index: 10000;
    width: 100%;
    backdrop-filter: blur(3px);
}
.navbar .navbar-brand{
    color: red;
    text-transform: uppercase;
    font-weight: 400;
    margin-right: auto;
}
.navbar .navbar-brand:hover{
    color: red;
    opacity: .6;
}
.navbar form{
    border-radius: 6px;
    border: 1px solid rgba(255,0,0,0.5);
    color: white;
}
.navbar form:hover{
    border: 1px solid rgba(255,0,0,.8);
}
.navbar input.form-control{
    background: transparent;
    border: none;
    box-shadow: none;
    color: rgba(255,255,255,0.8);
}
.navbar button{
    display: flex;
    justify-content: center;
    box-shadow: none;
}
.navbar button:focus,.navbar button:active{
    box-shadow: none;
    transform: scale(.8);
}
.navbar button svg{
    color: white;
    opacity: .5;
}
.navbar button svg:hover{
    opacity: 1;
}


.card img{
    border-radius: 6px;
}
.card .tag{
    height: 2.2rem;
    width: 2.2rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    background: rgb(20,20,20);
    padding: 1.1rem;
    border: 1.5px solid red;
    color: white;
    right: 0;
    top: 0;
    transform: translate(50%,-50%);
}
.card .tag p{
    margin: 0;
    font-size: .8rem;
    color: rgba(255,255,255,0.8);
}
.card .info{
    position: absolute;
    /* top: 100%; */
    left: 0;
    bottom: 0;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    border-radius: 6px;
    border: none;
    height: 100%;
    width: 100%;
    background: linear-gradient(transparent,rgb(0,0,0,0.9));
    padding: 1rem;
    color: white;
    opacity: 0;
    pointer-events: none;
    backdrop-filter: blur(1px);
    transition: .2s linear;
}
.card .info p{
    font-size: 0.8rem;
    color: rgba(255,255,255,0.5);
    font-weight: 600;

}
.card:hover {
    border-radius: 6px;
    transform: translateY(-10px) !important;
    border: 1.5px solid red;
    background-color: transparents;
}
.card:hover .info{
    opacity: 1;
    pointer-events: all;
}
.card .card-title{
    margin-bottom: 0.3rem !important;
}
.load{
    border: 1px solid red;
    color: red;
    min-width: 15rem;
    margin-top: 2rem;
    box-shadow: none;
    transition: .2s ease;
}
.load:hover,.load:focus{
    color: red;
    box-shadow: none;
    background: rgba(255,0,0,0.05);
}